import axios from 'axios';
import { createMarkup } from './js/createMarkup';//  
import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";
import LoadMoreBtn from './js/loadMoreBtn';//   loadmorebtn
import { getRefs } from './js/getRefs';//  
import NewAPI from './js/API'//  
import { onScroll, onTopButton } from './js/scroll';
import { emptySearchMessage, noImagesFoundMessage, imagesFoundMessage, endOfSearchMessage } from './js/notify';

onScroll();// 
onTopButton();
let lightbox;

const newApiService = new NewAPI();//       
const refs = getRefs();//  

refs.searchField.addEventListener('submit', handleSubmit);

function handleSubmit(e) {
    e.preventDefault();
    newApiService.query = e.currentTarget.searchQuery.value.trim();//   query   set    
    

    if (newApiService.query === '') {
        clearMarkup();
        loadMoreBtn.hide();
        return emptySearchMessage();
        
    } 

    loadMoreBtn.show();// 
    clearMarkup();// 
    newApiService.resetPage(); // c   1
    runSimpleLightBox();//  lightbox
    fetchAll();//
    

    
    
}

export const loadMoreBtn = new LoadMoreBtn({
    selector: '.load-more',
    hidden: true,
});//     loadmore  

loadMoreBtn.refs.button.addEventListener('click', onClickMoreBtn);//     

function onClickMoreBtn() {
    fetchAll();
}

function fetchAll() {
    loadMoreBtn.disable();
    newApiService.fetchArticles()
        .then(data => {
            console.log(data.totalHits)
            if (data.totalHits === 0) {
                noImagesFoundMessage();
            }
            if (newApiService.page === 2 && data.totalHits !== 0) {
                imagesFoundMessage(data.totalHits);
            }
            if (newApiService.page - 1 !== 1) {
                loadMoreBtn.hide();
                endOfSearchMessage();

            }
            if (data.totalHits < 40) {
                loadMoreBtn.hide();
            }

            loadMoreBtn.enable();
            runMarkup(data.hits); // 
            lightbox.refresh(); //  lightbox
            
    });
}

function runMarkup(c) {//  
    refs.getGallery.insertAdjacentHTML('beforeend', createMarkup(c));
}

function clearMarkup() {//  
    refs.getGallery.innerHTML = '';
}

function runSimpleLightBox() {
    lightbox = new SimpleLightbox('.gallery .gallery__link', {
        captionsData: 'alt',
        captionDelay: 250,
    });

}








